import Category from '../../util/category'
import request from '../../util/request'
import common from '../../util/common'

export default {
    name: 'Classify',
    data () {
        return {
            tabCur: 'male',
            sexTabs: [
                {
                    icon: 'cuIcon-male text-blue',
                    sex: 'male'
                },
                {
                    icon: 'cuIcon-female text-pink',
                    sex: 'female'
                }
            ],
            result: [],
            firstEnter: true
        }
    },
    onLoad () {
        this.queryClassify()
    },
    onTabItemTap () {
        if (!this.firstEnter) {
            this.queryClassify()
        }
    },
    methods: {
        tabSelect (e) {
            this.tabCur = e.currentTarget.dataset.sex
            this.queryClassify()
        },
        convertCategory (arg0) {
            return Category[this.tabCur][arg0]
        },
        convertSex (sex) {
            return common.getSex(sex)
        },
        convertTotal (arg0) {
            let result = arg0;
            (result || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
            result = '共' + result + '部'
            return result
        },
        queryClassify () {
            let params = {
                condition: {
                    sex: this.tabCur
                }
            }
            request.post('/novels/classify', params).then(data => {
                if (data.status === 200 && data.total > 0) {
                    this.result = data.data
                }
            }).finally(() => {
                if (this.firstEnter) {
                    this.firstEnter = false
                }
            })
        },
        searchBtn (category) {
            let params = {
                sex: this.tabCur,
                category: category
            }
            uni.navigateTo({
                url: '/pages/result/SearchResult?classify=' + JSON.stringify(params)
            })
        }
    }
}
